import { ReactElement, useRef, useEffect } from 'react'

interface Options<T> {
  time: number
  value: T
  action: (value: T) => void
  once?: boolean
}

enum State {
  INITIAL,
  ACTIVE,
  DONE
}

export function Debounce<T>({
  value,
  time,
  action,
  once = false
}: Options<T>): ReactElement<Options<T>> | null {
  /* eslint-disable-next-line */
  const timer = useRef(undefined as any)
  const state = useRef(State.INITIAL)
  useEffect(() => {
    if (state.current === State.INITIAL) {
      state.current = State.ACTIVE
    } else if (state.current === State.ACTIVE) {
      clearTimeout(timer.current)
      timer.current = setTimeout(() => {
        if (once) {
          state.current = State.DONE
        }
        action(value)
      }, time)
    }
    /* eslint-disable-next-line */
  }, [value, time])
  return null
}
