import React, { ReactElement, ReactNode } from 'react'
import css from '@emotion/css'
import styled from '@emotion/styled'
import { Formik, Field, FormikConfig, FieldProps, FormikErrors } from 'formik'
import AnimateHeight from 'react-animate-height'
import { MultifactorState } from '@lastpass/state/multifactor/state'
import { Actions, ActionTypes } from '@lastpass/state/multifactor/actions'
import { MultifactorForm } from './multifactor-form'
import {
  Description,
  DUOWebSDK,
  Links,
  Title,
  Spacing,
  TopSpacing
} from './components'
import {
  Container,
  TextInput,
  Checkbox,
  Button,
  SuccessMessage,
  ErrorDisplay,
  Spinner,
  ContainerLogo
} from '../components'
import { Change, DebounceFieldTouched } from '../effects'
import {
  MultifactorChallenge,
  MultifactorProvider
} from '@lastpass/types/multifactor'
import {
  makeConnector,
  Connector,
  LastPassComponent
} from '@lastpass/ui-helpers'

interface MultifactorPromptProps {
  challenge: MultifactorChallenge
  /**
   * Must be an img or svg element. ReactNode allows strings but ReactElement does not.
   */
  providerLogo?: ReactElement
  description?: ReactNode
  disableText?: string
}

const CodeFromContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`

function validateComputerName(
  values: MultifactorForm
): FormikErrors<MultifactorForm> {
  let errors: FormikErrors<MultifactorForm> = {}
  if (values.trustComputer && values.computerName === '') {
    errors.computerName = 'Please enter a name for this computer'
  }
  return errors
}

export const MultifactorPromptComponent: LastPassComponent<
  MultifactorPromptProps,
  MultifactorState,
  typeof Actions
> = ({ props, state, actions }) => {
  const challenge = props.challenge
  const config: FormikConfig<MultifactorForm> = {
    initialValues: {
      verificationCode: '',
      trustComputer: !!challenge.trustedDeviceLabel,
      computerName: challenge.trustedDeviceLabel || ''
    },
    onSubmit: values => {
      actions.submit(values.verificationCode, values.computerName)
    },
    validate: validateComputerName
  }
  return (
    <Container
      css={
        challenge.provider.duoWebSDK &&
        css`
          width: 662px;
        `
      }
    >
      <ContainerLogo />
      <Title providerLogo={props.providerLogo} actions={actions} />
      <Spacing className="e2e-description">
        {props.description || (
          <Description challenge={challenge} actions={actions} />
        )}
      </Spacing>
      <Formik
        {...config}
        render={formikprops => (
          <form onSubmit={formikprops.handleSubmit}>
            {challenge.provider.duoWebSDK ? (
              <DUOWebSDK />
            ) : (
              challenge.options.verificationCode && (
                <CodeFromContainer>
                  <div
                    css={css`
                      flex-grow: 2;
                      margin-right: 16px;
                    `}
                  >
                    <Field
                      name="verificationCode"
                      validate={value => {
                        if (
                          challenge.provider.type !==
                            MultifactorProvider.YUBIKEY &&
                          challenge.provider.type !== MultifactorProvider.DUO
                        ) {
                          if (isNaN(value)) {
                            return 'Must be numeric'
                          }
                          if (value.length < 6) {
                            return 'Must be at least 6 digits'
                          }
                        }
                      }}
                      render={(props: FieldProps<MultifactorForm>) => (
                        <>
                          <DebounceFieldTouched props={props} />
                          <TextInput
                            {...props.field}
                            label="Verification Code"
                            error={props.form.errors.verificationCode}
                            touched={props.form.touched.verificationCode}
                            type="password"
                            autoFocus={!challenge.options.outofband}
                          />
                        </>
                      )}
                    />
                  </div>

                  <Button
                    type="submit"
                    disabled={!formikprops.isValid}
                    id="authenticate"
                    css={css`
                      width: 150px;
                      font-size: 15px;
                    `}
                  >
                    Authenticate
                  </Button>
                </CodeFromContainer>
              )
            )}

            {challenge.options.trust && (
              <>
                <Field
                  name="trustComputer"
                  render={(props: FieldProps<MultifactorForm>) => (
                    <>
                      {challenge.options.outofband && (
                        <Change
                          value={props.field.value}
                          action={trust => {
                            if (!trust) {
                              actions.untrust()
                            }
                          }}
                        />
                      )}
                      <Checkbox
                        {...props.field}
                        label="Trust this computer for 30 days"
                      />
                    </>
                  )}
                />

                <AnimateHeight
                  height={formikprops.values.trustComputer ? 'auto' : 0}
                >
                  <Field
                    name="computerName"
                    render={(props: FieldProps<MultifactorForm>) => (
                      <>
                        <DebounceFieldTouched props={props} />
                        {challenge.options.outofband && (
                          <Change
                            value={props.field.value}
                            action={() => actions.trust(props.field.value)}
                          />
                        )}
                        <Spacing>
                          <TextInput
                            {...props.field}
                            label="Computer Name"
                            error={props.form.errors.computerName}
                            touched={props.form.touched.computerName}
                            autoComplete="off"
                          />
                        </Spacing>
                      </>
                    )}
                  />
                </AnimateHeight>
              </>
            )}
          </form>
        )}
      />
      <Links
        disableText={props.disableText}
        challenge={challenge}
        actions={actions}
      />
      {state.sentSMS && (
        <TopSpacing>
          <SuccessMessage>SMS Passcode sent!</SuccessMessage>
        </TopSpacing>
      )}
      {state.error && (
        <TopSpacing>
          <ErrorDisplay dismissable={true}>{state.error}</ErrorDisplay>
        </TopSpacing>
      )}
      <Spinner active={state.inProcess} />
    </Container>
  )
}

const connectMultifactorPrompt = (makeConnector as Connector<
  { multifactor: MultifactorState },
  ActionTypes
>)(MultifactorPromptComponent, state => state.multifactor, Actions)

export type MultifactorPrompt = ReturnType<typeof connectMultifactorPrompt>

export { connectMultifactorPrompt }
