import styled from '@emotion/styled'

interface ButtonProps {
  secondary?: boolean
}

export const Button = styled.button<ButtonProps>`
  text-transform: uppercase;
  display: block;
  width: 100%;
  outline: none;
  padding: 0;
  height: 50px;
  line-height: 50px;
  border-radius: 25px;
  background-color: ${props =>
    props.secondary ? props.theme.colors.white : props.theme.colors.red.light};
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.25);
  border: ${props =>
    props.secondary ? 'solid 1px ' + props.theme.colors.grey.lighter : 'none'};
  cursor: pointer;
  font-family: ${props => props.theme.fonts.primary};
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: ${props =>
    props.secondary
      ? props.theme.colors.grey.neutral
      : props.theme.colors.white};
  transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1),
    border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1),
    background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);

  &:hover,
  &:focus {
    background-color: ${props => (props.secondary ? '#f6f7f8' : '#af0809')};
  }

  &:active {
    background-color: ${props =>
      props.secondary ? props.theme.colors.grey.lighter : '#7e0400'};
  }

  &:disabled,
  &:disabled:hover {
    opacity: 0.5;
    cursor: default;
    background-color: ${props =>
      props.secondary
        ? props.theme.colors.white
        : props.theme.colors.red.light};
  }
`
