import React, { FunctionComponent, useState, KeyboardEvent } from 'react'

interface CapsLockProps {
  /* eslint-disable-next-line */
  renderInputComponent: (
    props: { onKeyPress: (e: KeyboardEvent<HTMLInputElement>) => void },
    isCaps: boolean
  ) => React.ReactElement
}

export const CapsLockSensitive: FunctionComponent<CapsLockProps> = props => {
  const [isCaps, setIsCaps] = useState(false)

  const inputProps = {
    onKeyPress: (e: KeyboardEvent<HTMLInputElement>) => {
      if (e.getModifierState('CapsLock')) {
        setIsCaps(true)
      } else {
        setIsCaps(false)
      }
    }
  }

  return props.renderInputComponent(inputProps, isCaps)
}
