import { Action } from '@lastpass/state'
import { FunctionWithReturnType } from '@lastpass/types'
import { Middleware } from 'redux'

/**
 * Defines any metadata that could dictate how something is tracked
 */
export interface TrackingMetadata {
  /**
   * Defines the unique source of the tracking event. For example, if there was
   * a DELETE_ITEM action, source would represent all the possible places in the
   * application where DELETE_ITEM could be dispatched (tile, context menu, popover,
   * infield)
   */
  source?: string
}

export type TrackerFunction<A> = (action: A, metadata: TrackingMetadata) => void
/* eslint-disable-next-line */
export type Tracker = [string, TrackerFunction<Action<any>>]

interface ActionWithMetadata extends Action<string> {
  metadata?: TrackingMetadata
}

/**
 * Contains the tracking functions for each action type
 */
const trackersByType: { [s: string]: TrackerFunction<Action<string>>[] } = {}

function addMetadata(
  actionCreator: FunctionWithReturnType<Action<string>>,
  metadata: TrackingMetadata
): FunctionWithReturnType<Action<string>> {
  return function(...args) {
    const action = actionCreator(...args)
    return { ...action, metadata }
  }
}

function removeMetadata(
  action: ActionWithMetadata
): [Action<string>, TrackingMetadata | undefined] {
  const metadata = action.metadata
  if (metadata) {
    const unwrapped = { ...action }
    delete unwrapped.metadata
    return [unwrapped, metadata]
  }
  return [action, metadata]
}

function saveTrackers(trackers?: Tracker[]) {
  if (trackers) {
    trackers.forEach(([type, func]) => {
      const trackersForType = trackersByType[type]
      if (trackersForType) {
        trackersByType[type] = trackersForType.concat(func)
      } else {
        trackersByType[type] = [func]
      }
    })
  }
}

function trackAction(action: Action<string>, metadata: TrackingMetadata) {
  const trackersForType = trackersByType[action.type]
  if (trackersForType) {
    trackersForType.forEach(tracker => {
      tracker(action, metadata)
    })
  }
}

export function track<A>(
  actionCreators: A,
  trackers?: Tracker[],
  metadata?: TrackingMetadata
): A {
  saveTrackers(trackers)
  if (metadata) {
    /* eslint-disable-next-line */
    const wrapped: any = { ...actionCreators }
    for (const key in wrapped) {
      wrapped[key] = addMetadata(wrapped[key], metadata)
    }
    return wrapped
  }
  return actionCreators
}

export const trackingMiddleware: Middleware = () => next => action => {
  const [unwrapped, metadata] = removeMetadata(action)
  trackAction(unwrapped, metadata || {})
  next(unwrapped)
}
