import React, { useEffect } from 'react'
import { Actions, ActionTypes } from '@lastpass/state/login/actions'
import { LoginState } from '@lastpass/state/login/state'
import {
  ExpansionPanel,
  Container,
  Checkbox,
  Button,
  Link,
  TextInput,
  Title,
  TitleText,
  Spinner,
  ErrorDisplay,
  ContainerLogo,
  CapsLockSensitive
} from '../components'
import { SuggestPasswordChange } from './components'
import { LoginForm } from './login-form'
import { Formik, FormikConfig, Field, FieldProps } from 'formik'
import styled from '@emotion/styled'
import css from '@emotion/css'
import { Multifactor, MultifactorPrompt } from '../multifactor'
import {
  Connector,
  makeConnector,
  LastPassComponent
} from '@lastpass/ui-helpers'
import { DebounceFieldTouched } from '../effects'
import { TypeaheadComponent } from '@lastpass/ui/components/Typeahead'
import { isProviderSupported } from '@lastpass/services/multifactor/supported'

export interface LoginProps {
  username?: string
  rememberUsername?: boolean
  enableRememberPassword?: boolean
  showTermsConditions?: boolean
  multifactorPrompt: MultifactorPrompt
}

const JustifiedDiv = styled.div`
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  color: ${props => props.theme.colors.blue.dark};
`

const CenterDiv = styled.div`
  text-align: center;
  margin-top: 20px;
`

const CenterLowerDiv = styled.div`
  text-align: center;
  margin-top: 10px;
`

function validateUsername(value: string) {
  var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  if (!re.test(value)) {
    return 'Please enter a valid email address'
  }
}

function validatePassword(value: string) {
  if (!value) {
    return 'Please enter a password'
  }
}

export const LoginComponent: LastPassComponent<
  LoginProps,
  LoginState,
  typeof Actions
> = ({ props, state, actions }) => {
  // Dispatches the intialize function when the component is mounted
  useEffect(() => {
    actions.initialize()
    /* eslint-disable-next-line */
  }, [])

  if (!state.initialized) {
    return null
  }

  const inProcess =
    state.inProcess ||
    (state.multifactor && !isProviderSupported(state.multifactor.provider.type))

  if (state.suggestPasswordChange) {
    return <SuggestPasswordChange actions={actions} username={state.username} />
  } else if (
    state.multifactor &&
    isProviderSupported(state.multifactor.provider.type)
  ) {
    return (
      <Multifactor
        challenge={state.multifactor}
        prompt={props.multifactorPrompt}
      />
    )
  }

  const initialValues: LoginForm = {
    username:
      props.username || (state.savedUsernames && state.savedUsernames[0]) || '',
    password: '',
    rememberUsername: props.rememberUsername || false
  }
  const config: FormikConfig<LoginForm> = {
    initialValues,
    onSubmit: (values, formik) => {
      actions.login(values.username, values.password, values.rememberUsername)
      formik.setFieldTouched('password', false)
      formik.setFieldValue('password', '')
    }
  }
  return (
    <Container>
      <ContainerLogo />
      <Title>
        <JustifiedDiv>
          <TitleText>Log In</TitleText>
          {!props.showTermsConditions && (
            <span
              css={css`
                text-transform: uppercase;
              `}
            >
              or&nbsp;
              <Link capitalize onClick={actions.createAccount}>
                Create an Account
              </Link>
            </span>
          )}
        </JustifiedDiv>
      </Title>

      <Formik
        {...config}
        render={formikprops => (
          <form onSubmit={formikprops.handleSubmit}>
            <Field
              name="username"
              validate={validateUsername}
              render={(fieldProps: FieldProps<LoginForm>) => (
                <TypeaheadComponent
                  suggestions={state.savedUsernames}
                  clearSuggestion={username => actions.removeUsername(username)}
                  onChange={value =>
                    fieldProps.form.setFieldValue(fieldProps.field.name, value)
                  }
                  renderInputComponent={typeaheadProps => (
                    <>
                      <DebounceFieldTouched props={fieldProps} />
                      <TextInput
                        {...typeaheadProps}
                        {...fieldProps.field}
                        // Workaround for https://github.com/moroshko/react-autosuggest/issues/318
                        innerRef={typeaheadProps.ref}
                        label="Email address"
                        error={fieldProps.form.errors.username}
                        autoFocus={!fieldProps.field.value}
                        spellCheck={false}
                        autoComplete="off"
                        touched={
                          // Set touched if Formik says so or the form had an initial value for username
                          fieldProps.form.touched.username ||
                          !!initialValues.username
                        }
                        onBlur={e => {
                          typeaheadProps.onBlur && typeaheadProps.onBlur(e)
                          fieldProps.field.onBlur(e)
                          if (!fieldProps.form.errors.username) {
                            actions.federatedLogin(fieldProps.field.value)
                          }
                        }}
                        onChange={e => {
                          typeaheadProps.onChange && typeaheadProps.onChange(e)
                          fieldProps.field.onChange(e)
                        }}
                      />
                    </>
                  )}
                />
              )}
            />
            <Field
              name="password"
              validate={validatePassword}
              render={(props: FieldProps<LoginForm>) => (
                <>
                  <DebounceFieldTouched props={props} />
                  <CapsLockSensitive
                    renderInputComponent={(capslockProps, capslockOn) => (
                      <TextInput
                        {...capslockProps}
                        {...props.field}
                        error={
                          props.form.errors.password ||
                          (capslockOn ? 'Caps lock is active' : undefined)
                        }
                        touched={
                          !props.form.isSubmitting &&
                          (props.form.touched.password || capslockOn)
                        }
                        autoFocus={!!props.form.values.username}
                        label="Master Password"
                        type="password"
                      />
                    )}
                  />
                </>
              )}
            />
            <Button type="submit" disabled={!formikprops.isValid}>
              Log In
            </Button>
            {!props.showTermsConditions && (
              <>
                <CenterDiv>
                  <Link capitalize onClick={actions.forgotPassword}>
                    Forgot Password?
                  </Link>
                </CenterDiv>
                <CenterLowerDiv>
                  <ExpansionPanel label="Advanced Settings">
                    <div
                      css={css`
                        text-align: left;
                      `}
                    >
                      <Field
                        name="rememberUsername"
                        render={({ field }) => (
                          <Checkbox
                            label="Remember Email"
                            {...field}
                            onClick={actions.rememberUsername}
                          />
                        )}
                      />
                      {props.enableRememberPassword && (
                        <Field
                          name="rememberPassword"
                          render={({ field }) => (
                            <Checkbox label="Remember Password" {...field} />
                          )}
                        />
                      )}
                      <Link
                        onClick={actions.oneTimePassword}
                        css={css`
                          margin-top: 10px;
                        `}
                      >
                        Log in using One Time Password
                      </Link>
                    </div>
                  </ExpansionPanel>
                </CenterLowerDiv>
              </>
            )}
          </form>
        )}
      />
      {props.showTermsConditions && (
        <CenterDiv>
          By completing this form, I agree to the&nbsp;
          <Link onClick={actions.openTerms}>Terms</Link> and&nbsp;
          <Link onClick={actions.openPrivacy}>Privacy Policy</Link>.
        </CenterDiv>
      )}
      <Spinner active={inProcess} />
      {state.error && (
        <ErrorDisplay dismissable={true}>{state.error}</ErrorDisplay>
      )}
    </Container>
  )
}

const connectLogin = (makeConnector as Connector<
  { login: LoginState },
  ActionTypes
>)(LoginComponent, state => state.login, Actions)

export type Login = ReturnType<typeof connectLogin>

export { connectLogin }
