import { FunctionComponent } from 'react'
import {
  ActionCreatorsMapObject,
  ActionDispatchers,
  ActionsUnion
} from '@lastpass/state'
import {
  track,
  TrackingMetadata,
  TrackerFunction
} from '@lastpass/middleware/tracking'
import { connect } from 'react-redux'
import { Theme } from '@lastpass/theme'

export type LastPassComponent<
  P,
  S,
  A extends ActionCreatorsMapObject = {}
> = FunctionComponent<{
  props: P
  state: S
  actions: ActionDispatchers<A>
}>

export interface Connector<AppState, ActionTypes extends string> {
  <Props, State, Actions extends ActionCreatorsMapObject>(
    component: LastPassComponent<Props, State, Actions>,
    selector: (appState: AppState) => State,
    actions: Actions
  ): <T extends AppState>(
    trackers?: [ActionTypes, TrackerFunction<ActionsUnion<Actions>>][],
    metadata?: TrackingMetadata
  ) => FunctionComponent<Props>
}

export function makeConnector<
  Props,
  State,
  Actions extends ActionCreatorsMapObject,
  AppState,
  ActionTypes extends string
>(
  component: LastPassComponent<Props, State, Actions>,
  selector: (appState: AppState) => State,
  actions: Actions
) {
  return function<T extends AppState>(
    trackers?: [ActionTypes, TrackerFunction<ActionsUnion<Actions>>][],
    metadata?: TrackingMetadata
  ): FunctionComponent<Props> {
    return connect(
      selector,
      /*eslint-disable-next-line*/
      track(actions, trackers as any, metadata),
      (stateProps, dispatchProps, ownProps: Props) => {
        return {
          props: ownProps,
          actions: dispatchProps,
          state: stateProps
        }
      }
      /*eslint-disable-next-line*/
    )(component as any) as any
  }
}

export type FunctionComponentWithTheme<P> = FunctionComponent<
  P & { theme: Theme }
>
