import { Fetch } from '@lastpass/http'
import { Platform } from '@lastpass/types/platform'

export interface TrackingPlatformMetadata {
  /**
   * Defines the platform where the
   */
  platform: Platform
}

export interface SegmentEvent {
  event: string
  properties?: {
    /* eslint-disable-next-line */
    [s: string]: any
  }
}

export type SegmentService = (data: SegmentEvent) => void

export function createSegmentService(
  metadata: TrackingPlatformMetadata,
  fetch: Fetch
): SegmentService {
  return function(data): void {
    fetch.lmiapi('segment/send-event', {
      platform: metadata.platform,
      ...data,
      properties: data.properties || []
    })
  }
}
