import React, { FunctionComponent, ButtonHTMLAttributes } from 'react'
import { ReactComponent as BackIcon } from '@lastpass/assets/svg/icon-back.svg'
import styled from '@emotion/styled'
import css from '@emotion/css'

const Button = styled.button`
  height: 20px;
  width: 20px;
  box-sizing: content-box;
  background-color: transparent;
  padding: 10px;
  margin: -10px;
  cursor: pointer;
  color: ${props => props.theme.colors.blue.dark};
  border: none;
  outline: none;
  text-align: left;
  :focus {
    filter: drop-shadow(0 0 2px ${props => props.theme.colors.blue.light});
  }
  :hover {
    color: ${props => props.theme.colors.blue.light};
    filter: drop-shadow(0 0 2px ${props => props.theme.colors.blue.light});
  }
`

export const BackButton: FunctionComponent<
  ButtonHTMLAttributes<HTMLButtonElement>
> = props => (
  <Button {...props} aria-label="Back">
    <BackIcon
      css={css`
        vertical-align: middle;
      `}
    />
  </Button>
)
