import React from 'react'
import styled from '@emotion/styled'
import { Chevron } from '../../components'
import { withTheme } from 'emotion-theming'
import { FunctionComponentWithTheme } from '@lastpass/ui-helpers'

interface Props {
  open: boolean
}

const Container = styled.div`
  position: relative;
`

const Arrow = styled.div`
  position: absolute;
  right: 0px;
  top: 10px;
`

const InputWrapperBase: FunctionComponentWithTheme<Props> = props => (
  <Container>
    {props.children}
    <Arrow>
      <Chevron
        color={props.theme.colors.grey.light}
        direction={props.open ? 'up' : 'down'}
      />
    </Arrow>
  </Container>
)

export const InputWrapper = withTheme(InputWrapperBase)
