import React, { FunctionComponent } from 'react'
import { SMSData } from '@lastpass/types/multifactor'
import { ActionDispatchers } from '@lastpass/state'
import { Actions } from '@lastpass/state/multifactor/actions'
import { Link } from '@lastpass/ui/components'

export const SMSBackup: FunctionComponent<{
  sms: SMSData
  actions: ActionDispatchers<typeof Actions>
}> = ({ sms, actions }) => (
  <Link
    id="smsCode"
    onClick={() => {
      actions.sms(sms)
    }}
  >
    {sms.nextCode
      ? 'Next SMS passcode starts with ' + sms.nextCode + ' (send more)'
      : 'Send SMS Passcode'}
  </Link>
)
