import React, { FunctionComponent, ReactElement } from 'react'
import styled from '@emotion/styled'
import { ActionDispatchers } from '@lastpass/state'
import { Actions } from '@lastpass/state/multifactor/actions'
import {
  Title as TitleComponent,
  TitleText,
  BackButton
} from '../../components'

const FlexBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 6px;
`

export const Title: FunctionComponent<{
  providerLogo?: ReactElement
  actions: ActionDispatchers<typeof Actions>
}> = ({ providerLogo, actions }) => (
  <TitleComponent>
    <FlexBox>
      <BackButton
        onClick={() => {
          actions.cancel()
        }}
      />
      {providerLogo}
      <TitleText>Multifactor Authentication</TitleText>
    </FlexBox>
  </TitleComponent>
)
