import styled from '@emotion/styled'

interface Options {
  capitalize?: boolean
}

export const Link = styled.button<Options>`
  text-align: left;
  text-transform: ${props => (props.capitalize ? 'uppercase' : 'none')};
  background: none;
  border: none;
  padding: 0;
  outline: 0;
  font-size: 14px;
  line-height: 1.4em;
  font-weight: ${props => (props.capitalize ? '600' : 'normal')};
  color: ${props => props.theme.colors.blue.light};
  text-decoration: none;
  cursor: pointer;

  &:hover,
  &:active,
  &:focus {
    text-decoration: underline;
  }
`
