import React, { useState, useEffect } from 'react'
import Autosuggest, { AutosuggestProps } from 'react-autosuggest'
import { InputWrapper } from './InputWrapper'
import { Suggestion } from './Suggestion'
import { withTheme } from 'emotion-theming'
import { FunctionComponentWithTheme } from '@lastpass/ui-helpers'

interface TypeaheadProps {
  suggestions: string[]
  /* eslint-disable-next-line */
  renderInputComponent: (props: any) => React.ReactNode
  onChange: (value: string) => void
  clearSuggestion?(suggestion: string): void
}

const TypeaheadComponentBase: FunctionComponentWithTheme<
  TypeaheadProps
> = props => {
  const [value, setValue] = useState('')
  const [suggestions, setSuggestions] = useState([] as string[])

  const filterSuggestions = (value: string) => {
    setSuggestions(
      props.suggestions.filter(suggestion => {
        return suggestion.startsWith(value)
      })
    )
  }

  /* eslint-disable-next-line */
  useEffect(() => filterSuggestions(value), [props.suggestions])

  var autosuggestProps: AutosuggestProps<string> = {
    suggestions,
    onSuggestionsFetchRequested: params => filterSuggestions(params.value),
    onSuggestionsClearRequested: () => {
      setSuggestions([])
    },
    getSuggestionValue: suggestion => suggestion,
    /* eslint-disable-next-line */
    renderSuggestion: suggestion => (
      <Suggestion value={suggestion} onRemove={props.clearSuggestion} />
    ),
    /* eslint-disable-next-line */
    renderInputComponent: inputProps => (
      <InputWrapper open={suggestions.length > 0}>
        {props.renderInputComponent(inputProps)}
      </InputWrapper>
    ),
    inputProps: {
      value: value,
      onChange: (formEvent, changeEvent) => {
        setValue(changeEvent.newValue)
        props.onChange(changeEvent.newValue)
      }
    },
    theme: {
      container: {
        position: 'relative'
      },
      suggestionsContainer: {
        display: 'none'
      },
      suggestionsContainerOpen: {
        display: 'block',
        width: '100%',
        position: 'absolute',
        backgroundColor: '#fff',
        overflow: 'hidden',
        border: '1px solid #aaa',
        borderRadius: '14px',
        boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.25)',
        top: '52px',
        zIndex: 1
      },
      suggestionsList: {
        margin: 0,
        padding: 0
      },
      suggestionHighlighted: {
        backgroundColor: props.theme.colors.blue.lighter
      }
    }
  }

  return props.suggestions.length > 0 ? (
    <Autosuggest {...autosuggestProps} />
  ) : (
    <>{props.renderInputComponent({})}</>
  )
}

export const TypeaheadComponent = withTheme(TypeaheadComponentBase)
