import React, { FunctionComponent } from 'react'
import { MultifactorPrompt } from '../MultifactorPrompt'
import { MultifactorChallenge } from '@lastpass/types/multifactor'
import { getRetina } from '@lastpass/ui-helpers/get-retina'
import MicrosoftAuthLogo from '@lastpass/assets/png/multifactor/authlogo-microsoft.png'
import MicrosoftAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-microsoft@2x.png'
import { DescriptionSpacing, ProviderLogo } from '../components'

interface Props {
  challenge: MultifactorChallenge
  prompt: MultifactorPrompt
}

export const MicrosoftMultifactor: FunctionComponent<Props> = props => {
  const Prompt = props.prompt
  return (
    <Prompt
      challenge={props.challenge}
      providerLogo={
        <ProviderLogo
          src={getRetina(MicrosoftAuthLogo, MicrosoftAuthLogoRetina)}
        />
      }
      description={
        <DescriptionSpacing>
          Run the Microsoft Authenticator application on your mobile device and
          enter the verification code in the input box below.
        </DescriptionSpacing>
      }
      disableText="I've lost my Microsoft Authenticator device"
    />
  )
}
