import React, { useState, FunctionComponent } from 'react'
import styled from '@emotion/styled'
import AnimateHeight from 'react-animate-height'
import { Chevron } from '@lastpass/ui/components/Chevron'

interface ExpansionPanelProps {
  /**
   * The label of the expansion panel. Displayed at the bottom with an arrow indicating whether it is expanded or not.
   */
  label: string
  /**
   * Specifies whether or not the panel should be initially expanded
   */
  expanded?: boolean
}

const Button = styled.button`
  border: none;
  outline: none;
  height: 20px;
  font-size: 15px;
  color: ${props => props.theme.colors.grey.neutral};
  background: transparent;
  cursor: pointer;

  &:focus {
    text-decoration: underline;
  }
`

export const ExpansionPanel: FunctionComponent<ExpansionPanelProps> = props => {
  const [expanded, setExpanded] = useState(props.expanded || false)
  return (
    <div>
      <Button type="button" onClick={() => setExpanded(!expanded)}>
        {props.label}
        <Chevron direction={expanded ? 'up' : 'down'} />
      </Button>
      <AnimateHeight height={expanded ? 'auto' : 0}>
        {props.children}
      </AnimateHeight>
    </div>
  )
}
