import React, { FunctionComponent } from 'react'
import {
  MultifactorProvider,
  MultifactorChallenge
} from '@lastpass/types/multifactor'
import {
  GoogleMultifactor,
  MicrosoftMultifactor,
  YubikeyMultifactor
} from './providers'
import { logoCSS } from './components'
import { MultifactorPrompt } from './MultifactorPrompt'
import { ReactComponent as LastPassMFAIcon } from '@lastpass/assets/svg/multifactor/lastpass-mfa.svg'

import LastpassAuthLogo from '@lastpass/assets/png/multifactor/authlogo-lastpass.png'
import LastpassAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-lastpass@2x.png'
import SalesforceAuthLogo from '@lastpass/assets/png/multifactor/authlogo-salesforce.png'
import SalesforceAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-salesforce@2x.png'
import SecureAuthLogo from '@lastpass/assets/png/multifactor/authlogo-secureauth.png'
import SecureAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-secureauth@2x.png'
import SecureIdAuthLogo from '@lastpass/assets/png/multifactor/authlogo-securid.png'
import SecureIdAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-securid@2x.png'
import SymantecVipAuthLogo from '@lastpass/assets/png/multifactor/authlogo-symantec.png'
import SymantecVipAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-symantec@2x.png'
import ToopherAuthLogo from '@lastpass/assets/png/multifactor/authlogo-toopher.png'
import ToopherAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-toopher@2x.png'
import DuoAuthLogo from '@lastpass/assets/png/multifactor/authlogo-duo.png'
import DuoAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-duo@2x.png'
import TranksaktAuthLogo from '@lastpass/assets/png/multifactor/authlogo-transakt.png'
import TranksaktAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-transakt@2x.png'
import { getRetina } from '@lastpass/ui-helpers/get-retina'

interface Props {
  challenge: MultifactorChallenge
  prompt: MultifactorPrompt
}

function getLogoSource(type: MultifactorProvider) {
  switch (type) {
    case MultifactorProvider.LASTPASS_AUTHENTICATOR:
      return getRetina(LastpassAuthLogo, LastpassAuthLogoRetina)
    case MultifactorProvider.SALESFORCE:
      return getRetina(SalesforceAuthLogo, SalesforceAuthLogoRetina)
    case MultifactorProvider.SECURE_AUTH:
      return getRetina(SecureAuthLogo, SecureAuthLogoRetina)
    case MultifactorProvider.SECURID:
      return getRetina(SecureIdAuthLogo, SecureIdAuthLogoRetina)
    case MultifactorProvider.SYMANTEC_VIP:
      return getRetina(SymantecVipAuthLogo, SymantecVipAuthLogoRetina)
    case MultifactorProvider.TOOPHER:
      return getRetina(ToopherAuthLogo, ToopherAuthLogoRetina)
    case MultifactorProvider.DUO:
      return getRetina(DuoAuthLogo, DuoAuthLogoRetina)
    case MultifactorProvider.TRANSAKT:
      return getRetina(TranksaktAuthLogo, TranksaktAuthLogoRetina)
  }
}

export const Multifactor: FunctionComponent<Props> = props => {
  const Prompt = props.prompt
  switch (props.challenge.provider.type) {
    case MultifactorProvider.MICROSOFT_AUTHENTICATOR:
      return <MicrosoftMultifactor {...props} />
    case MultifactorProvider.GOOGLE_AUTHENTICATOR:
      return <GoogleMultifactor {...props} />
    case MultifactorProvider.YUBIKEY:
      return <YubikeyMultifactor {...props} />
    case MultifactorProvider.LASTPASS_MFA:
      return (
        <Prompt
          challenge={props.challenge}
          providerLogo={<LastPassMFAIcon css={logoCSS} />}
        />
      )
    default:
      const logoSource = getLogoSource(props.challenge.provider.type)
      return (
        <Prompt
          challenge={props.challenge}
          providerLogo={<img css={logoCSS} src={logoSource} />}
        />
      )
  }
}
