import React, { FunctionComponent, InputHTMLAttributes } from 'react'
import styled from '@emotion/styled'

interface CheckboxProps extends InputHTMLAttributes<HTMLInputElement> {
  label: string
}

const Label = styled.label`
  width: 100%;
  cursor: pointer;
  position: relative;
  display: inline-block;
  margin-top: 10px;
  color: ${props => props.theme.colors.blue.dark};
`

const Input = styled.input`
  opacity: 0;
  width: 0;
  height: 0;
`

const Slider = styled.span`
  width: 25px;
  height: 12px;
  float: right;
  margin: auto;
  border-radius: 6px;
  border: solid 1px #c5c8ca;
  background-color: #f3f3f4;
  transition: 0.4s;
  box-sizing: border-box;
  &:before {
    border: 1px solid transparent;
    position: absolute;
    content: '';
    height: 14px;
    width: 14px;
    right: 11px;
    top: -2px;
    background-color: ${props => props.theme.colors.grey.lighter};
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
    transition: 0.4s;
    border-radius: 50%;
  }

  input:checked + & {
    border: solid 1px ${props => props.theme.colors.grey.neutral};
    background-color: #eff9ff;
  }

  input:focus + &:before {
    border: 1px solid ${props => props.theme.colors.blue.neutral};
    box-shadow: 0 0 4px ${props => props.theme.colors.blue.light};
  }

  input:checked + &:before {
    transform: translateX(13px);
    background-color: #22303b;
  }
`

export const Checkbox: FunctionComponent<CheckboxProps> = props => (
  <Label>
    <span>{props.label}</span>
    <Input type="checkbox" {...props} name={props.name || props.label} />
    <Slider />
  </Label>
)
