import React, { FunctionComponent, DOMAttributes } from 'react'
import styled from '@emotion/styled'
import { ReactComponent as CloseIcon } from '@lastpass/assets/svg/icon-close.svg'

const Container = styled.div`
  cursor: pointer;
  position: absolute;
  height: 100%;
  right: 10px;
  padding: 0 10px;
  top: 0;
  color: ${props => props.theme.colors.grey.light};

  &:hover {
    color: ${props => props.theme.colors.black};
  }
`

const CloseIconComponent = styled(CloseIcon)`
  align-self: center;
  height: 100%;
`

export const RemoveButton: FunctionComponent<
  Pick<DOMAttributes<HTMLDivElement>, 'onClick'>
> = ({ onClick }) => (
  <Container
    onClick={e => {
      if (typeof onClick === 'function') {
        onClick(e)
        e.stopPropagation()
      }
    }}
  >
    <CloseIconComponent />
  </Container>
)
