import { userReducer } from '@lastpass/state/user/reducer'
import { loginReducer } from '@lastpass/state/login/reducer'
import { multifactorReducer } from '@lastpass/state/multifactor/reducer'
import { combineReducers, createStore, applyMiddleware } from 'redux'
import { trackingMiddleware } from '@lastpass/middleware/tracking'
import { performanceMiddleware } from '@lastpass/middleware/performance'

const appReducer = combineReducers({
  user: userReducer,
  login: loginReducer,
  multifactor: multifactorReducer
})
export type AppState = ReturnType<typeof appReducer>

export const appStore = createStore(
  appReducer,
  applyMiddleware(trackingMiddleware, performanceMiddleware)
)
