import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { ActionDispatchers } from '@lastpass/state'
import { Actions } from '@lastpass/state/login/actions'
import { Confirmation, FlexButton } from '../../components'

const ButtonSpacer = styled.div`
  width: 20px;
`

export const SuggestPasswordChange: FunctionComponent<{
  username: string
  actions: ActionDispatchers<typeof Actions>
}> = ({ username, actions }) => {
  return (
    <Confirmation
      title="A Quick Reminder"
      text={
        <div>
          <p>
            {
              "We detected that your LastPass master password hasn't been changed in a long time."
            }
          </p>
          <p>To keep your account safe, we recommend changing it.</p>
        </div>
      }
    >
      <FlexButton
        secondary
        id="e2e-not-now"
        onClick={actions.ignoreSuggestChangePassword}
      >
        Later
      </FlexButton>
      <ButtonSpacer />
      <FlexButton
        id="e2e-change"
        onClick={() => actions.changePassword(username)}
      >
        Change now
      </FlexButton>
    </Confirmation>
  )
}
