import React, { useState, FunctionComponent } from 'react'
import { css } from '@emotion/core'
import styled from '@emotion/styled'
import { ReactComponent as WarningIcon } from '@lastpass/assets/svg/icon-warning.svg'

interface ErrorDisplayProps {
  dismissable?: boolean
  children: React.ReactNode
}

const ErrorDiv = styled.div`
  position: relative;
  display: block;
  border-radius: 4px;
  background-color: ${props => props.theme.colors.orange.neutral};
  padding: 19px;
  padding-left: 70px;
  margin-top: 10px;
`

const ErrorLabel = styled.label`
  font-size: 14px;
  text-align: left;
  font-weight: 700;
  color: ${props => props.theme.colors.white};
  font-family: 'Open Sans Bold', sans-serif;
`

const DismissLink = styled.span`
  text-decoration: underline;
  &:hover {
    cursor: pointer;
  }
`
export const ErrorDisplay: FunctionComponent<ErrorDisplayProps> = props => {
  const [visible, setVisible] = useState(true)
  if (!visible) {
    return null
  }
  return (
    <ErrorDiv className="error">
      <WarningIcon
        css={css`
          position: absolute;
          top: 50%;
          left: 22px;
          transform: translateY(-50%);
          border-radius: 50%;
          display: inline-block;
        `}
      />
      <ErrorLabel>
        {props.children}{' '}
        {props.dismissable && (
          <DismissLink className="dismiss" onClick={() => setVisible(false)}>
            Dismiss
          </DismissLink>
        )}
      </ErrorLabel>
    </ErrorDiv>
  )
}
