import React from 'react'
import ReactDOM from 'react-dom'
import { connectLogin } from '@lastpass/ui/login'
import { appStore, AppState } from './app-store'
import { Provider } from 'react-redux'
import { ExtensionConfig } from './extension-config'
import { createLoginSegmentTrackers } from '@lastpass/ui/login/tracking/segment'
import { createFetch } from '@lastpass/http'
import { createSegmentService } from '@lastpass/server/tracking/segment'
import { connectMultifactorPrompt } from '@lastpass/ui/multifactor'
import { ThemeProvider } from 'emotion-theming'
import { LastPassTheme } from '@lastpass/theme'

export function initialize(config: ExtensionConfig) {
  if (!config.lastpassUrl) {
    throw new Error('BASE_URL not defined')
  }

  // Services
  const extensionFetch = createFetch(config.lastpassUrl)
  const segmentService = createSegmentService(
    { platform: config.platform },
    extensionFetch
  )

  // UI
  const Login = connectLogin<AppState>(
    createLoginSegmentTrackers(segmentService)
  )
  const MultifactorPrompt = connectMultifactorPrompt<AppState>()

  function App() {
    return (
      <ThemeProvider theme={LastPassTheme}>
        <Provider store={appStore}>
          <Login multifactorPrompt={MultifactorPrompt} />
        </Provider>
      </ThemeProvider>
    )
  }

  ReactDOM.render(<App />, document.getElementById('root'))
}
