import React, { FunctionComponent } from 'react'
import { MultifactorPrompt } from '../MultifactorPrompt'
import { MultifactorChallenge } from '@lastpass/types/multifactor'
import YubikeyAuthLogo from '@lastpass/assets/png/multifactor/authlogo-yubico.png'
import YubikeyAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-yubico@2x.png'
import { getRetina } from '@lastpass/ui-helpers/get-retina'
import { DescriptionSpacing, ProviderLogo } from '../components'

interface Props {
  challenge: MultifactorChallenge
  prompt: MultifactorPrompt
}

export const YubikeyMultifactor: FunctionComponent<Props> = props => {
  const Prompt = props.prompt
  return (
    <Prompt
      challenge={props.challenge}
      providerLogo={
        <ProviderLogo src={getRetina(YubikeyAuthLogo, YubikeyAuthLogoRetina)} />
      }
      description={
        <>
          <DescriptionSpacing>
            1. Insert your YubiKey in the USB-port with the USB-contact facing
            upward
          </DescriptionSpacing>
          <DescriptionSpacing>
            2. Wait until your YubiKey touch-button shines with a steady light
          </DescriptionSpacing>
          <DescriptionSpacing>
            3. Hold your fingertip on the touch-button for 1 second
          </DescriptionSpacing>
        </>
      }
      disableText="I've lost my Yubikey device"
    />
  )
}
