import React, { FunctionComponent } from 'react'
import { MultifactorChallenge } from '@lastpass/types/multifactor'
import { Link } from '../../components'
import { DescriptionSpacing } from './Spacing'
import { Actions } from '@lastpass/state/multifactor/actions'
import { ActionDispatchers } from '@lastpass/state'

const OutOfBandDescription: FunctionComponent<{
  auto: boolean
  actions: ActionDispatchers<typeof Actions>
}> = ({ auto, actions }) => {
  if (auto) {
    return (
      <DescriptionSpacing>
        Please complete multifactor authentication on your phone or mobile
        device.
      </DescriptionSpacing>
    )
  } else {
    return (
      <Link onClick={() => actions.sendPush()}>
        Click here to initiate multifactor authentication on your phone or
        mobile device.
      </Link>
    )
  }
}

export const Description: FunctionComponent<{
  challenge: MultifactorChallenge
  actions: ActionDispatchers<typeof Actions>
}> = ({ challenge, actions }) => {
  if (challenge.provider.duoWebSDK) {
    return null
  } else if (challenge.textOverride) {
    return <p>{challenge.textOverride}</p>
  } else if (challenge.options.outofband) {
    return (
      <>
        <OutOfBandDescription
          auto={challenge.options.outofbandauto}
          actions={actions}
        />
        {challenge.options.verificationCode && (
          <DescriptionSpacing>
            Alternatively, enter a verification code in the box below:
          </DescriptionSpacing>
        )}
      </>
    )
  }
  return (
    <DescriptionSpacing>Enter a passcode in the box below:</DescriptionSpacing>
  )
}
