import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { ActionDispatchers } from '@lastpass/state'
import { Actions } from '@lastpass/state/multifactor/actions'
import { Link } from '../../components'
import { SMSBackup, TopSpacing } from '.'
import { MultifactorChallenge } from '@lastpass/types/multifactor'

const SmallSpacing = styled.div`
  margin: 10px 0;
`

export const Links: FunctionComponent<{
  disableText?: string
  challenge: MultifactorChallenge
  actions: ActionDispatchers<typeof Actions>
}> = ({ disableText, challenge, actions }) => {
  if (
    challenge.options.disable ||
    (challenge.sms && !challenge.provider.duoWebSDK)
  ) {
    return (
      <TopSpacing>
        {challenge.options.disable && (
          <SmallSpacing>
            <Link onClick={() => actions.disable(challenge)}>
              {disableText || 'Disable Multifactor'}
            </Link>
          </SmallSpacing>
        )}

        {challenge.sms && !challenge.provider.duoWebSDK && (
          <SmallSpacing>
            <SMSBackup sms={challenge.sms} actions={actions} />
          </SmallSpacing>
        )}
      </TopSpacing>
    )
  }
  return null
}
