import * as Login from '@lastpass/state/login/actions'
import { SegmentService } from '@lastpass/server/tracking/segment'
import { timeSince } from '@lastpass/middleware/performance'

export function createLoginSegmentTrackers(
  segment: SegmentService
): Login.Tracker[] {
  return [
    [
      Login.INITIALIZE,
      () => {
        segment({
          event: 'login_viewed'
        })
      }
    ],
    [
      Login.LOGIN,
      () => {
        segment({
          event: 'login_clicked'
        })
      }
    ],
    [
      Login.MFA_CHALLENGE,
      action => {
        if (action.type === Login.MFA_CHALLENGE) {
          segment({
            event: 'multifactor_viewed',
            properties: {
              mfa: action.payload.data && action.payload.data.provider.type
            }
          })
        }
      }
    ],
    [
      Login.SUCCESS,
      action => {
        if (action.type === Login.SUCCESS) {
          segment({
            event: 'login_success',
            properties: {
              mfa:
                action.payload.challenge &&
                action.payload.challenge.provider.type,
              time: timeSince(Login.LOGIN)
            }
          })
        }
      }
    ]
  ]
}
