import React, { FunctionComponent } from 'react'
import { MultifactorPrompt } from '../MultifactorPrompt'
import { MultifactorChallenge } from '@lastpass/types/multifactor'
import GoogleAuthLogo from '@lastpass/assets/png/multifactor/authlogo-google.png'
import GoogleAuthLogoRetina from '@lastpass/assets/png/multifactor/authlogo-google@2x.png'
import { getRetina } from '@lastpass/ui-helpers/get-retina'
import { DescriptionSpacing, ProviderLogo } from '../components'

interface Props {
  challenge: MultifactorChallenge
  prompt: MultifactorPrompt
}

export const GoogleMultifactor: FunctionComponent<Props> = props => {
  const Prompt = props.prompt
  return (
    <Prompt
      challenge={props.challenge}
      providerLogo={
        <ProviderLogo src={getRetina(GoogleAuthLogo, GoogleAuthLogoRetina)} />
      }
      description={
        <DescriptionSpacing>
          Run the Google Authenticator application on your mobile device and
          enter the verification code in the input box below.
        </DescriptionSpacing>
      }
      disableText="I've lost my Google Authenticator device"
    />
  )
}
